<template>
  <div class="block">
    <el-date-picker
      v-model="pickerTime"
      type="date"
      placeholder="选择日期"
      :picker-options="pickerOptions">
    </el-date-picker>
  </div>
</template>

<script>
    export default {
      name: "DayTimePicker",
      data() {
        return {
          pickerTime: '',
          pickerOptions: {
            disabledDate(time) {
              return time.getTime() > Date.now();
            }
          },
        };
      },



    }
</script>

<style scoped>

</style>



